import React, { Component } from 'react';
import './App.css';
import btn_icon_back_covid19resource from './images/btn_icon_back_covid19resource.png';

// UI framework component imports
import Appbar from 'muicss/lib/react/appbar';

export default class COVID19ResourceScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      webcontent: '(null)',
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  onClick_elText = (ev) => {
    window.open((this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_web'] : ''), '_blank');
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    const style_elText = {
      fontSize: 12.3,
      color: '#1436ff',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    let sourceUrl_webcontent = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_web'] : '');
    
    const style_elWebcontent = {
      pointerEvents: 'auto',
     };
    
    return (
      <div className="AppScreen COVID19ResourceScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elText">
            <div className="systemFontBold" style={style_elText} onClick={this.onClick_elText} >
              <div>{this.props.locStrings.covid19resource_text_408223}</div>
            </div>
          </div>
        </div>
        <Appbar className="navBar">
          <div className="title">COVID-19 Resource</div>  <div className="backBtn" onClick={ (ev)=>{ this.props.appActions.goBack() } }><img src={btn_icon_back_covid19resource} alt="" style={{width: '50%'}} /></div>
        </Appbar>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <iframe className="elWebcontent" style={style_elWebcontent} src={sourceUrl_webcontent}  />
          </div>
        </div>
      </div>
    )
  }
  
}
