import React, { Component } from 'react';
import './App.css';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class NewResource extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      txtTitle: '',
      txtDesc: '',
      txtwebsite: '',
      txtEmail: '',
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  textInputChanged_txtTitle = (event) => {
    this.setState({txtTitle: event.target.value});
    this.props.appActions.updateDataSlot("ds_name", event.target.value);
  }
  
  textAreaChanged_txtDesc = (event) => {
    this.setState({txtDesc: event.target.value});
  }
  
  textInputChanged_txtwebsite = (event) => {
    this.setState({txtwebsite: event.target.value});
  }
  
  textInputChanged_txtEmail = (event) => {
    this.setState({txtEmail: event.target.value});
  }
  
  onClick_elButton = (ev) => {
    let newVal = this.state.txtTitle;
    this.props.appActions.updateDataSlot('ds_name', newVal);
  
    this.sendData_button_to_newresources();
  
    alert("Resource has been sent. Thank you!");
  
    // Clear internal state for data-providing elements in this component
    this.setState({
      text: (<div></div>),
      text_plainText: "",
      txtTitle: "",
      textCopy2: (<div></div>),
      textCopy2_plainText: "",
      txtDesc: "",
      textCopy4: (<div></div>),
      textCopy4_plainText: "",
      txtwebsite: "",
      textCopy6: (<div></div>),
      textCopy6_plainText: "",
      txtEmail: "",
    });
  
  }
  
  
  sendData_button_to_newresources = () => {
    const dataSheet = this.props.appActions.getDataSheet('newresources');
  
    let row = this.props.dataSheetRow || {
    };
    row = { ...row, 
      email: this.state.txtEmail,
      title: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_name'] : ''),
      name: this.state.txtTitle,
      desc: this.state.txtDesc,
      link: this.state.txtwebsite,
    };
    if (this.props.dataSheetId === dataSheet.id) {
      this.props.appActions.updateInDataSheet('newresources', row);
    } else {
      this.props.appActions.addToDataSheet('newresources', row);
    }
  }
  
  
  render() {
    const style_elText = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    
    const style_elTxtTitle = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    const style_elTextCopy2 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    
    const style_elTxtDesc = {
      display: 'block',
      backgroundColor: 'white',
      borderColor: 'lightGray',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    const style_elTextCopy4 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    
    const style_elTxtwebsite = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    const style_elTextCopy6 = {
      fontSize: 15.2,
      color: '#0093d5',
      textAlign: 'left',
     };
    
    const style_elTxtEmail = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    const style_elButton = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    return (
      <div className="NewResource appBg">
        <div className="layoutFlow">
          <div className="elText">
            <div className="systemFontBold" style={style_elText}>
              <div>{this.props.locStrings.newresource_text_759919}</div>
            </div>
          </div>
          
          <div className="elTxtTitle">
            <input className="baseFont" style={style_elTxtTitle} type="text" placeholder={this.props.locStrings.newresource_field_597142} onChange={this.textInputChanged_txtTitle} value={this.state.txtTitle}  />
          </div>
          
          <div className="elTextCopy2">
            <div className="systemFontBold" style={style_elTextCopy2}>
              <div>{this.props.locStrings.newresource_textcopy2_804562}</div>
            </div>
          </div>
          
          <div className="elTxtDesc">
            <textarea className="baseFont" style={style_elTxtDesc}  placeholder={this.props.locStrings.newresource_textarea_765040} onChange={this.textAreaChanged_txtDesc} value={this.state.txtDesc}  />
          </div>
          
          <div className="elTextCopy4">
            <div className="systemFontBold" style={style_elTextCopy4}>
              <div>{this.props.locStrings.newresource_textcopy4_202115}</div>
            </div>
          </div>
          
          <div className="elTxtwebsite">
            <input className="baseFont" style={style_elTxtwebsite} type="text" placeholder={this.props.locStrings.newresource_fieldcopy_287232} onChange={this.textInputChanged_txtwebsite} value={this.state.txtwebsite}  />
          </div>
          
          <div className="elTextCopy6">
            <div className="systemFontBold" style={style_elTextCopy6}>
              <div>{this.props.locStrings.newresource_textcopy6_21628}</div>
            </div>
          </div>
          
          <div className="elTxtEmail">
            <input className="baseFont" style={style_elTxtEmail} type="email" placeholder={this.props.locStrings.newresource_fieldcopy2_848229} onChange={this.textInputChanged_txtEmail} value={this.state.txtEmail}  />
          </div>
          
          <div className="elButton">
            <Button className="actionFont" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.props.locStrings.newresource_button_92745}
            </Button>
          </div>
        </div>
        
      </div>
    )
  }
  
}
