import React, { Component } from 'react';
import './App.css';
import NewResource from './NewResource';
import btn_icon_back_addresource from './images/btn_icon_back_addresource.png';

// UI framework component imports
import Appbar from 'muicss/lib/react/appbar';

export default class AddResourceScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
    {
      let dataSheet = this.props.appActions.dataSheets['newresources'];
      let serviceOptions = this.props.appActions.serviceOptions_newresources;
      if ( !this.props.appActions.dataSheetLoaded['newresources']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-newresources", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet5(dataSheet, serviceOptions, true);
        this.props.appActions.dataSheetLoaded['newresources'] = true;
      }
      this._dataSheetLoadTimer_newresources = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-newresources", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet5(dataSheet, serviceOptions, false);
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this._dataSheetLoadTimer_newresources);
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    
    return (
      <div className="AppScreen AddResourceScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="hasNestedComps elNewResource">
            <div>
              <NewResource ref={(el)=> this._elNewResource = el} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
            </div>
          </div>
        </div>
        <Appbar className="navBar">
          <div className="title">Add Resource</div>  <div className="backBtn" onClick={ (ev)=>{ this.props.appActions.goBack() } }><img src={btn_icon_back_addresource} alt="" style={{width: '50%'}} /></div>
        </Appbar>
        
      </div>
    )
  }
  
}
